.head {
  width: 100%;
  background-color: gray;
  justify-content: center;
  margin-bottom: 10px;
  padding: 20px 0;
  display: flex;
}

.search-form {
  justify-content: center;
  align-items: center;
  gap: 10px;
  display: flex;
}

.search-form input {
  height: 40px;
  border: none;
  border-radius: 5px;
  outline: none;
  padding: 2px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.search-form button, .load-more {
  color: #fff;
  height: 40px;
  cursor: pointer;
  background-color: #07c;
  border: none;
  border-radius: 5px;
  padding: 10px;
  transition: background-color .3s;
}

.search-form button:hover, .load-more:hover {
  background-color: #0066b2;
}

.main {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  margin-bottom: 10px;
  display: flex;
}

.photo-card {
  text-align: center;
  border: 1px solid gray;
  border-radius: 5px;
  flex-direction: column;
  flex-basis: calc(25% - 7.5px);
  display: flex;
}

.photo-card img {
  width: 100%;
}

.photo-card a {
  height: 70%;
  overflow: hidden;
}

.info {
  justify-content: space-around;
  display: flex;
}

.info p {
  font-size: 12px;
}

.info b {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.load-more {
  visibility: hidden;
  pointer-events: none;
  margin: 0 auto;
  display: none;
}

.visible {
  visibility: visible;
  pointer-events: auto;
  display: block;
}

